import React, { useEffect, useState } from 'react';
import useForceUpdate from "../helper/forceupdate"
import initial_board from "./initial.js"
import "./board.css"
import Pawn from "../Player/pawn"
import Player from "../Player/player"


var list = []
const Board = () => {
    const [Players, setPlayers] = useState([])
    const [Board, setBoard] = useState(initial_board)
    const [view, setview] = useState(true)
    const [classname, setclass] = useState("")
    const forceUpdate = useForceUpdate();
    useEffect(() => {
        setInterval(forceUpdate,10)
        var players = []
        players.push(Player("blue"))
        players.push(Player("green"))
        players.push(Player("red"))
        players.push(Player("yellow"))
        setPlayers(players)
        // var currentstate=Board
        // currentstate.forEach((row)=>{
        //     row.forEach((box)=>{
        //         if(box.path){
        //             box["class"]+=" path"
        //         }
        //     })
        // })
        // console.log(JSON.stringify(currentstate))
    }, [])

    useEffect(() => {
        var b = Board
        b.forEach((k)=>{
            k.forEach((v)=>{
                v.value=""
            })
        })
        var players = Players
        players.forEach((player) =>{
            player.position.forEach((position) =>{
                b[position.pos[0]][position.pos[1]].value=player.pawn
            })
        })
        setBoard(b)
    }, [Players]);
    const updateColor=(y,x)=>{
        var current_board =Board
        current_board[y][x].class +=" " + classname
        setBoard(current_board)
    }
    if(view){
        return(
            <div className="board">
                <Pawn bg="blue"/>
            <div>
                <button onClick={()=>setview(false)}>Edit</button>
            </div>
            {Board.map((row, index1) => {
                return (
                <div className="row">
                    {row.map((box, index2) => {
                        if(index1===7 && index2 === 7){
                            return(
                                <div className={box.class} >
                                    <div className={box.child[0].class}></div>
                                    <div className={box.child[1].class}></div>
                                    <div className={box.child[2].class}></div>
                                    <div className={box.child[3].class}></div>
                                </div>
                            )
                        }
                    return <div className={box.class} onClick={()=>{
                        list.push([index1,index2])
                        console.log(JSON.stringify(list))
                    }}> {box.value} </div>
                    })}
                </div>
                );
            })}
        </div>
        )
    }
    return (
        <>
        <div className="board">
        <div>
                <input type="text" value={classname} onChange={(e)=>setclass(e.target.value)}/>
                <button onClick={()=>console.log(JSON.stringify(Board))}>print</button>
                <button onClick={()=>setview(true)}>view</button>
        </div>
            {Board.map((row, index1) => {
                return (
                <div className="row">
                    {row.map((box, index2) => {
                        if(index1===7 && index2 === 7){
                            return(
                                <div className={box.class} >
                                    <div className={box.child[0].class}></div>
                                    <div className={box.child[1].class}></div>
                                    <div className={box.child[2].class}></div>
                                    <div className={box.child[3].class}></div>
                                </div>
                            )
                        }
                    return <div className={box.class} onClick={()=>updateColor(index1,index2)} > {box.value} </div>;
                    })}
                </div>
                );
            })}
        </div>
        </>

    );
}

export default Board;

