import {initialblue,blueplayer,initialred,redplayer,initialgreen,greenplayer,initialyellow,yellowplayer} from "../board/initial"

const Player = (color) =>{
    switch(color){
        case "blue":
            return { position:initialblue,pawn:blueplayer,dice:0,turn:false}
        case "red":
            return { position:initialred,pawn:redplayer,dice:0,turn:false}
        case "green":
            return { position:initialgreen,pawn:greenplayer,dice:0,turn:false}
        case "yellow":
            return { position:initialyellow,pawn:yellowplayer,dice:0,turn:false}
        default:
            break
    }
}

export default Player;
